'use client';

import { sendMessage, type FormState } from '@/lib/actions';
import { useActionState, useEffect, useRef, useTransition } from 'react';
import { Button } from './ui/button';
import { Input } from './ui/input';
import { Textarea } from './ui/textarea';
import { Label } from './ui/label';
import { Loader2, Send, CheckCircle, AlertCircle } from 'lucide-react';
import { Card, CardContent } from './ui/card';

const initialState: FormState = {
  success: false,
};

export default function SendMessageForm() {
  const [state, formAction] = useActionState(sendMessage, initialState);
  const formRef = useRef<HTMLFormElement>(null);

  useEffect(() => {
    if (state.success) {
      formRef.current?.reset();
    }
  }, [state.success]);

  return (
    <div className="mx-auto mt-8 max-w-xl">
      <Card>
        <CardContent className="p-6">
          <form ref={formRef} action={formAction} className="space-y-6">
            <div className="space-y-2">
              <Label htmlFor="recipient">Recipient's Name</Label>
              <Input
                id="recipient"
                name="recipient"
                placeholder="e.g., Alex"
                required
                aria-describedby="recipient-error"
              />
              {state.errors?.recipient && (
                <p id="recipient-error" className="text-sm text-red-500">
                  {state.errors.recipient}
                </p>
              )}
            </div>
            <div className="space-y-2">
              <Label htmlFor="message">Your Anonymous Message</Label>
              <Textarea
                id="message"
                name="message"
                placeholder="Write something that will make them smile..."
                rows={5}
                required
                aria-describedby="message-error"
              />
              {state.errors?.message && (
                <p id="message-error" className="text-sm text-red-500">
                  {state.errors.message}
                </p>
              )}
            </div>
            <div className="flex flex-col gap-2 sm:flex-row">
              <SubmitButton />
            </div>
          </form>
          {state.success && (
            <div
              className="mt-4 flex items-center rounded-lg border border-green-200 bg-green-50 p-4 text-sm text-green-700 animate-in fade-in-0"
              role="alert"
            >
              <CheckCircle className="mr-2 h-4 w-4" />
              <p>
                Success! Your message was sent to{' '}
                <span className="font-semibold">{state.recipient}</span>.
              </p>
            </div>
          )}
          {state.message && !state.success && (
             <div
             className="mt-4 flex items-center rounded-lg border border-red-200 bg-red-50 p-4 text-sm text-red-700 animate-in fade-in-0"
             role="alert"
           >
             <AlertCircle className="mr-2 h-4 w-4" />
             <p>{state.message}</p>
           </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}

function SubmitButton() {
    const [isPending, startTransition] = useTransition();

    useEffect(() => {
        const form = document.querySelector('form');
        const handleSubmit = (event: Event) => {
            event.preventDefault();
            const formData = new FormData(event.target as HTMLFormElement);
            startTransition(async () => {
                const action = (event.target as HTMLFormElement).action;
                await fetch(action, {
                    method: 'POST',
                    body: formData,
                });
            });
        };

        if (form) {
             // This is a workaround for using useTransition with form actions in some React versions.
             // We manually trigger the form submission inside a transition.
             // This can be simplified by using `useFormStatus` from React DOM Canary.
        }
    }, []);

    return (
        <Button type="submit" className="w-full flex-1" aria-disabled={isPending}>
            {isPending ? (
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
            ) : (
                <Send className="mr-2 h-4 w-4" />
            )}
            Send Message
        </Button>
    );
}
